import { TypesFilters } from '../const';
import { filter } from '../utils/filter';

const generateFilter = (events) =>
  Object.entries(filter).map(([typeFilter, filterEvents]) => ({
    type: typeFilter,
    count: filterEvents(events).length,
    isDisabled: filterEvents(events).length === 0,
    isChecked: typeFilter === TypesFilters.EVERYTHING,
  }));

export { generateFilter };
